<template>
    <div>
      <header class="count_top header">
        <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
        <span>店铺详情</span>
      </header>
      <div class="store_top">
        <section>
          <p class="store_name">{{info.name}}</p>
          <p class="store_top_c">开店金额{{info.money}}元,邀请补贴{{info.bili}}%</p>
          <p>订单补贴{{info.bili_1}}%,开店补贴{{info.jifen_bili}}%</p>
          <a href="javascript:void(0)" @click="setShop">立即开店</a>
        </section>
        <img src="../../assets/shopImage/store.png" alt="">
      </div>
      <p class="inter"></p>
      <div class="store_desc">
        <h2>店铺特色</h2>
        <p v-html="info.dianpu_tese"></p>
      </div>
      <div class="store_desc">
        <h2>店铺描述</h2>
        <p v-html="info.dianpu_desc"></p>
      </div>
      <pop ref="pop" @show="payType"></pop>
    </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import pop from  '../common/pop'
  export default {
  data (){
    return {
      id: '',
      src: '',
      payInfo: {},
      info: {}
    }
  },
  methods:{
    payType(val) {
      //0 余额 1 其他
      if (val === '0') {
        this.post('home/Pay/payForMoney', {
          id: this.payInfo.id,
          type: this.payInfo.type
        }).then(res => {
          if (res.data.code === 10000){
            this.$router.push('/info');
            Toast({
              message: res.data.info,
              duration: 1000
            });
          } else {
            Toast({
              message: res.data.info,
              duration: 1000
            });
          }
        })
      } else {
        let token = localStorage.getItem('token');
        location.href = `http://flm.yunkzx.com/home/Shop/order_pay?token=${token}&id=${this.payInfo.id}&type=${this.payInfo.type}`;
      }
    },
    back(){
      this.$router.go(-1);
    },
    setShop(){
      this.$refs.pop.value = '';
      this.post('home/Shop/setDianpu', {
        id: this.id
        }).then(res => {
          if(res.data.code === 10000){
            this.$refs.pop.popupVisible = true;
            this.payInfo = res.data.info;
          } else {
            Toast({
              message: res.data.info,
              duration: 1000
            });
          }
      })
    },
    // 获取店铺详情
    getStoreInfo(id){
      this.post('home/Shop/getDianpuInfo', {
        dianpu_id: id
      }).then(res => {
          if (res.data.code === 10000) {
            this.info = res.data.info;
          }
      })
    },
  },
  components:{pop},
  mounted(){
    this.id = this.$route.params.id;
    this.getStoreInfo(this.id);
  }
}
</script>
<style scoped>
@import "../../../static/header.css";
  .store_top {
    padding: 0.3rem 0.2rem;
    background-color: #ff6f00;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .store_top p {
    color: #FFFFFF;
    font-size: 0.24rem;
  }
 .store_top .store_name {
    font-size: 0.3rem;
  }
  .store_top .store_top_c {
    margin: 0.3rem 0 0.16rem 0;
  }
  .store_top p:before {
    display: inline-block;
    content: '';
    height: 0.1rem;
    width: 0.1rem;
    border-radius: 50%;
    background-color: #FFFFFF;
    margin-right: 0.1rem;
    vertical-align: middle;
  }
  .store_top a {
    display: inline-block;
    width: 1.6rem;
    height: 0.45rem;
    line-height: 0.45rem;
    background-color: #FFFFFF;
    color: #ff6f00;
    text-align: center;
    font-size: 0.28rem;
    border-radius: 2px;
    margin-top: 0.3rem;
    margin-left: 0.2rem;
  }
  .store_top img{
    width: 2.12rem;
    height: 1.9rem;
    margin-right: 0.3rem;
  }
  .inter {
    height: 0.2rem;
    background-color: #F2F2F2;
  }
  .store_desc {
    padding: 0 0.2rem;
  }
  .store_desc h2 {
    height: 0.68rem;
    font-size: 0.28rem;
    color: #111111;
    line-height: 0.68rem;
  }
  .store_desc p {
    padding: 0.3rem 0;
    font-size: 0.26rem;
    color: #444444;
    line-height: 0.34rem;
  }
  .store_desc h3 {
    font-size: 0.26rem;
    color: #333333;
  }
  .store_desc section {
    padding: 0.2rem 0;
  }
  .store_desc section p {
    font-size: 0.26rem;
    color: #444444;
    line-height: 0.34rem;
    padding: 0.1rem 0;
  }
</style>



